@forward 'normalize';

$primary-color: #212E5B;
$secondary-color: #C41D48;
$black-color: #000000;
$white-color: #FFFFFF;
$primary-color-light: #EFF7FF;

$font-heading: 'Rubik', sans-serif;
$font-normal: 400;
$font-bold: 700;

$wrap-width: 1350px;
$wrap-width-narrow: 900px;
$wrap-padding: 2rem;
$border-radius: 0.28rem;

$border-style: 1px solid #D9D9D9;

* {
    box-sizing: border-box;
}

html {
    scroll-behavior: smooth;
    scroll-padding-top: 5rem;
}

body {
    font-family: $font-heading;
    font-size: 18px;
    overflow-x: hidden;
    padding-top: 5rem;

    &.admin-bar .site-header {
        top: 32px; /* Moves the header down */
    }
    
}

.screen-reader-text {
    position: absolute;
    left: -9999px;
    top: 0;
}

.fancy-anchor a, p a, .contact-list a, a.fancy {
    display: inline-block;
    &:after {
        content:'';
        width: 0;
        height: 2px;
        display: block;
        background: $white-color;
        border-radius: 2px;
        position: relative;
        top: 2px;
        transition: 1s all ease;
    }

    &:hover:after {
        width: 100%;
        transition: 1s all ease;
    }
}

p, .contact-list {
    line-height: 1.4;

    a {
        color: $primary-color;
        text-decoration: none;
        font-weight: $font-bold;

        &:after {
            background: $primary-color;
            top: 0px;
        }
    }
}

h1, h2, h3 {
    color: $primary-color;
    text-transform: uppercase;
}

h2 {
    font-size: 2rem;
}

dl.contact-list {
    dt {
        font-weight: $font-bold;
        margin-bottom: 0.25rem;
    }

    dd {
        margin-bottom: 0.5rem;
        margin-left: 0;
        font-weight: 300;
    }
}

.ul-dots {
    position: relative;
     &:after {
        content: '...';
        color: $secondary-color;
        position: absolute;
        left: 0;
        right: 0;
        bottom: -1.5rem;
        font-size: 2.7rem;
        font-family: serif;
     }
}

.fixed-offer-btn {
    transform: translateY(-50%);
    writing-mode: vertical-rl;
    text-transform: uppercase;
    position: fixed;
    left: -1.2rem;
    top: 50%;
    background: $secondary-color;
    color: $white-color;
    padding: 1rem 1rem 1rem 2rem;
    z-index: 9999; 
    border-radius: 0 $border-radius $border-radius 0;
    text-decoration: none;
    font-weight: $font-bold;
    transition: all 0.5s ease;
    @media (max-width: 980px) {
        right: -1.2rem;
        left: auto;
        top: auto;
        bottom: 10%;
        transform: rotate(180deg);
    }

    &:hover {
        left: -0.7rem;
        transition: all 0.5s ease;
        @media (max-width: 980px) {
            left: auto;
            right: -0.7rem;
        }
    }
}

.nav-dots {
    position: absolute;
    bottom:-1rem;
    right: 2rem;
    background-color: $white-color;
    border-radius: $border-radius $border-radius 0 0;
    z-index: 9999;
    display: flex;
    gap: 1rem;
    padding: 0.5rem 0.5rem 0 1rem;
    align-items: center;

    button {
        box-shadow: none;
    }

    .my-nav {
        display: flex;
        gap: 0.5rem;
        button {
            padding: 0.5rem 1rem;
        }
    }

    .my-dots {
        display: flex;
        gap: 0.5rem;
        button {
            padding: 0;
            width: 10px;
            height: 10px;
            border-radius: 5px;

            &.active {
                background: $secondary-color;
            }
        }
    }
}

.searchbox {
    position: relative;

    i {
        position: absolute;
        top: 0.75rem;
        left: 0.6rem;
        color: $white-color;
        font-size: 2rem;
    }

    .search-field {
        background-color: $primary-color;
        width: 100%;
        padding: 1rem 1rem 1rem 3.5rem;
        border: none;
        border-radius: $border-radius;
        color: $white-color;
    }

}



main button, .btn, input[type="submit"] {
    background: $primary-color;
    color: $white-color;
    border: none;
    padding: 1.11111rem 1.66667rem;
    border-radius: $border-radius;
    font-size: 1rem;
    text-transform: lowercase;
    font-weight: $font-bold;
    display: inline-block;
    cursor: pointer;
    box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.25);
    transition: all 0.5s ease;
    width: auto;
    justify-self: start;
    text-decoration: none;

    &:hover {
        background: $secondary-color;
    }

    &.red {
        background: $secondary-color;

        &:hover {
            background: $primary-color;
        }
    }
}

ul {

    padding-left: 1.5rem;
    line-height: 1.4;

    li {
        list-style: none;
        position: relative;
        &:before {
            content: '';
            width: 10px;
            height: 4px;
            border-radius: 2px;
            background-color: $secondary-color;
            display: block;
            position: absolute;
            top: 0.65rem;
            left: -1.5rem;
        }
    }

}

.wrap {
    max-width: $wrap-width;
    margin: 0 auto;
    width: 100%;
    padding: 0 $wrap-padding;
}

.wrap-narrow {
    max-width: $wrap-width-narrow;
    margin: 0 auto;
    width: 100%;
    padding: 0 $wrap-padding;
}

.wrap-wide {
    position: relative;
    width: calc(100% - $wrap-padding);
    border-radius: 0 $border-radius $border-radius 0;
    @media (min-width: $wrap-width) {
        width: calc(100% - ((100% - $wrap-width) / 2) - $wrap-padding);
    }
    @media (min-width: 1700px) {
        width: 1500px;
        left: calc((100% - $wrap-width) / 2 - (1500px - $wrap-width + $wrap-padding));
        border-radius: $border-radius;
    }

    .full-img {
        @media (min-width: 1700px) {
            border-radius: $border-radius;
        }
    }

    &.wrap-reverse {
        left: $wrap-padding;
        border-radius: $border-radius 0 0 $border-radius;
        @media (min-width: $wrap-width) {
            left: calc((100% - $wrap-width) / 2 + $wrap-padding);
        }
        @media (min-width: 1700px) {
            border-radius: $border-radius;
        }
    }

    .wrap {
        max-width: calc($wrap-width - $wrap-padding);

        &.wrap-absolute {
            position: absolute;
            top: 0;
            right: 0;
        }

        &.with-narrow {
            max-width: calc($wrap-width - 2 * $wrap-padding);
            padding: 0;
            margin: 0 0 0 auto;
            @media (max-width: $wrap-width) and (min-width: calc($wrap-width - $wrap-padding)) {
                margin-left: calc($wrap-padding - 0.5 * ($wrap-width - 100vw));
            }
    
            @media (max-width: calc($wrap-width - $wrap-padding)) and (min-width: calc($wrap-width-narrow + $wrap-padding)) {
                margin-left: calc($wrap-padding / 2);
            }

            @media (max-width: calc($wrap-width-narrow + $wrap-padding)) and (min-width: $wrap-width-narrow) {
                margin-left: calc($wrap-padding / 2 - 0.5 * ($wrap-width-narrow + $wrap-padding - 100vw));
    
            }

            @media (max-width: $wrap-width-narrow) {
                margin-left: 0;
            }
        }
    }
    .wrap-narrow {
        width: 100%;  
    }
}

.site-header {
    position: fixed;
    width: 100%;
    top: 0;
    background: $white-color;
    z-index: 9000;

    &.scrolling {
        
        nav .header-logo .ma-logo {
            height: 0;
            overflow: hidden;
            transition: all 0.5s ease;
        }
        nav .header-logo .site-title {
            margin-bottom: 0;
            transition: all 0.5s ease;
        }
    }

    nav {
        display: flex;
        justify-content: space-between;
        position: relative;
        @media (max-width: 980px) {
            align-items: center;
        }

        .header-logo {
            text-decoration: none;
            padding: 1rem 0;
            .site-title {
                font-size: 1.11rem;
                line-height: 1.2;
                text-transform: uppercase;
                color: $secondary-color;
                margin: 0 0 0.5rem;
                font-weight: $font-bold;

                span {
                    color: $primary-color;
                }
            }

            .ma-logo {
                color: $primary-color;
                font-size: 0.77778rem;
                text-transform: uppercase;
                display: flex;
                align-items: center;
                gap: 0.5rem;
                height: 19px;
                transition: all 0.5s ease;
                margin: 0;
                img {
                    width: 27px;
                }
            }
        }

        .header-right {
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            padding-bottom: 0.5rem;

            @media (max-width: 980px) {
                position: absolute;
                top: 100%;
                right: -2rem;
                background-color: $primary-color-light;
                border-radius: 0 0 0 $border-radius;
                width: 18.5rem;
                transform: translateX(100%);
                transition: 1s all ease;
            }

            &.mobile-menu-open {
                @media (max-width: 980px) {
                    transition: 1s all ease;
                    transform: translateX(0);
                }               
            }

            .header-right-top {
                display: flex;
                justify-content: flex-end;
                align-items: center;
                gap: 1rem;
                @media (max-width: 980px) {
                    flex-direction: column;
                    align-items: flex-start;
                }

                .info-line {
                    background: $primary-color;
                    color: $white-color;
                    font-size: 0.77778rem;
                    display: flex;
                    align-items: center;
                    padding: 0.5rem 1rem;
                    gap: 1rem;
                    border-radius: 0 0 $border-radius $border-radius;
                    @media (max-width: 980px) {
                        flex-direction: column;
                        width: 100%;
                        border-radius: 0 0 0 $border-radius;
                        align-items: flex-start;
                        gap: 0.3rem;
                        padding: 0.8rem 1rem;
                    }

                    a {
                        color: $white-color;
                        text-decoration: none;
                        font-size: 1.33333rem;
                        font-weight: $font-bold;
                    }
                }

                .wpml-ls {

                    padding: 0;
                    border: none;
                
                    ul {
                    display: flex;
                    gap: 0.5rem;
                    margin: 0;
                    padding: 0;
                    @media (max-width: 980px) {
                        padding-left: 1rem;
                    }

                        li {

                            &::before {
                                display: none;
                            }

                            a {
                                text-decoration: none;
                                color: $primary-color;
                                font-size: 0.9rem;
                                padding: 0;
                            }

                            &.wpml-ls-current-language a {
                                color: $secondary-color;
                                font-weight: $font-bold;
                            }

                        }

                    }


                }
            }

            .header-right-bottom {
                display: flex;
                justify-content: flex-end;
                align-items: center;
                gap: 1rem;
                padding-top: 0.5rem;
                @media (max-width: 980px) {
                    flex-direction: column;
                    padding-top: 1rem;
                    align-items: flex-start;
                    padding-left: 1rem;
                }

                ul.menu {
                    display: flex;
                    gap: 1rem;
                    padding: 0;
                    margin: 0;
                    .menu-item {

                        &::before {
                            display: none;
                        }

                    }
                }

                .main-menu #main-menu-list {
                    @media (max-width: 980px) {
                        flex-direction: column;
                        gap: 1rem;
                        align-items: flex-start;
                    }

                    a {
                        color: $primary-color;
                        text-decoration: none;
                        text-transform: uppercase;
                        font-size: 0.88889rem;
                        font-weight: $font-bold;
                        display: flex;
                        flex-direction: column;
                        
                        .menu-underline {
                            width: 100%;
                            display: flex;
                            gap: 4px;

                            .menu-dot {
                                width: 4px;
                                height: 4px;
                                border-radius: 2px;
                                background: $secondary-color;
                            }
                            .dot-underline {
                                transition: all 1s;
                            }

                        }

                        &:hover {
                            .dot-underline {
                                width: calc(100% - 16px);
                            }
                            
                        }

                    }

                    .current-menu-item a {
                        .dot-underline {
                            width: 100%;
                        }  
                    }

                }

                .social-links #social-links-list {
                    @media (max-width: 980px) {
                        gap: 0.75rem;
                    }
                    a {
                        color: $primary-color;
                        @media (max-width: 980px) {
                            font-size: 2rem;
                        }

                        &:hover {
                            color: $secondary-color
                        }
                    }
                }

            }

        }

        .mobile-menu-trigger {
            display: none;
            width: 3rem;
            height: 3rem;
            border: none;
            border-radius: 2rem;
            background: $primary-color;
            color: $white-color;

            @media (max-width: 980px) {
                display: inline-block;
            }
        }
    }
}

main {
    padding: 2rem 0;
}

.featured-img-section {

    .wrap {
        height: 100%;
        display: flex;
        align-items: center;
    }

    h2 {
        font-size: 2.66rem;
        text-transform: uppercase;
        color: $white-color;
        @media (max-width: 800px) {
            font-size: 1.66rem;
        }

        &:before, &:after {
            content: '';
            width: 7rem;
            height: 0.5rem;
            border-radius: .5rem;
            background: $white-color;
            display: block;
            @media (max-width: 800px) {
                width: 5rem;
                height: 0.25rem;
            }
        }

        &:before {
            margin-bottom: 1rem;
        }

        &:after {
            margin-top: 1rem;
        }
    }

    figure {
        width: 100%;

        img {
            width: 100%;
            height: auto;
            border-radius: 0 $border-radius $border-radius 0;
        }
    }
}

/* HOME */

.content-section {
    padding: 3rem 0;

    .what-we-do {
        display: flex;
        margin-top: 3rem;
        justify-content: space-between;
        @media (max-width: 600px) {
            justify-content: flex-start;
            align-items: flex-start;
            flex-direction: column;
            gap: 1rem;
        }

        .services {
            display: flex;
            flex: 0 0 auto;
            align-items: center;

            h3 {
                font-size: 1.333rem;
                margin: 0;
                @media (max-width: 800px) {
                    font-size: 1.2rem;
                }

                br {
                    @media (max-width: 600px) {
                        display: none;
                    }
                }
            }

            img {
                @media (max-width: 800px) {
                    width: 65px;
                }
                @media (max-width: 600px) {
                    margin-right: 1rem;
                }
            }
        }
    }
}

.services {
    display: flex;
    flex: 0 0 auto;
    align-items: center;

    h3 {
        font-size: 1.333rem;
        margin: 0;
        @media (max-width: 800px) {
            font-size: 1.2rem;
        }

        br {
            @media (max-width: 600px) {
                display: none;
            }
        }
    }

    img {
        @media (max-width: 800px) {
            width: 65px;
        }
        @media (max-width: 600px) {
            margin-right: 1rem;
        }
    }
}

.products-home {
    margin: 2rem 0;
     h2 {
        color: $primary-color;
        text-transform: uppercase;
        text-align: center;
     }

     .product-tiles {
        display: flex;
        justify-content: center;
        flex-direction: row;
        flex-wrap: wrap;
        padding: 2rem 0;

        .row-break {
            height: 0;
            &:nth-child(4) {
                flex-basis: 100%;
            }
        }

        .product-tile {
            padding: 1rem;
            color: $primary-color;
            text-transform: lowercase;
            font-size: 1.333rem;
            text-align: center;
            text-decoration: none;
            font-weight: $font-bold;

            span {
                width: 10rem;
                display: block;
                text-align: center;
            }

            figure {
                background: url('../img/tilted-rect-light.svg') no-repeat;
                background-size: 100%;
                background-position: center;
                padding: 1rem 1rem 0;
                margin-bottom: 1rem;

                img{
                    transition: all 1s ease;
                }
            }

            &:hover {
                color: $secondary-color;
                img {
                    transform: scale(1.1);
                    transition: all 1s ease;
                }
            }

        }

     }
}

.numbers {
    color: $white-color;
    margin: 2rem 0;
    .wrap-wide {
        background: $primary-color;
        padding-top: 2rem;
        padding-bottom: 2rem;
        position: relative;

        &:after {
            content: '...';
            color: $secondary-color;
            position: absolute;
            left: 1.5rem;
            bottom: -1.45rem;
            font-size: 5rem;
            font-family: serif;
         }

        .wrap {
            margin-right: auto;
            margin-left: 0;

            h2 {
                margin: 0;
                color: $white-color;
            }

            .numbers-container {
                display: flex;
                justify-content: space-around;
                flex-wrap: wrap;
                
                .number-tile {
                    text-align: center;
                    padding: 1rem 2rem 0;
                    @media (max-width: 1050px) {
                        padding: 1rem 1rem 0;
                    }

                    h3 {
                        font-size: 5.5rem;
                        color: $secondary-color;
                        font-weight: $font-bold;
                        margin: 0;
                        @media (max-width: 1050px) {
                            font-size: 3.5rem;
                        }
                    }

                    p {
                        margin: 0;
                    }
                }
            }
        }
    }
}

.applications {
    margin: 4rem 0;
    h2 {
        color: $primary-color;
        text-transform: uppercase;
        text-align: center;
    }
    .application-tiles {
        padding: 3rem 0;
        display: flex;
        flex-direction: column;
        gap: .6rem;

        .at-rows {
            display: flex;
            justify-content: center;
            @media (max-width: 1000px) {
                flex-direction: column;
                gap: 0.6rem;
            }

            &.at-row-1 {
                margin-right: auto;
                @media (max-width: 1000px) {
                    margin: 0 auto;
                }
            }

            &.at-row-2 {
                margin-left: auto;
                @media (max-width: 1000px) {
                    margin: 0 auto;
                }
            }

            .application-tile {
                display: flex;
                flex-direction: row-reverse;
                align-items: center;
                text-decoration: none;
                margin-right: -3rem;
                @media (max-width: 1150px) {
                    margin-right: -2.25rem;
                }
                @media (max-width: 1000px) {
                    margin-right: 0rem;
                }

                &:hover {
                    h3 {
                        color: $secondary-color;
                    }
                }
    
                &:nth-child(2) {
                    flex-direction: row;
                    margin-left: -3rem;
                    margin-right: auto;
                    @media (max-width: 1150px) {
                        margin-left: -2.25rem;
                    }
                    @media (max-width: 1000px) {
                        margin-left: 0rem;
                    }

                    h3 {
                        text-align: left;
                        margin-right: auto;
                        margin-left: -3.2rem;
                        @media (max-width: 1150px) {
                            margin-left: -2.5rem;
                        }
                        @media (max-width: 600px) {
                            margin-left: 0;
                        }
                    }

                    span {
                        
                        &.indent-1 {
                            padding-left: 0.8rem;
                            padding-right: 0;
                            @media (max-width: 600px) {
                                padding-left: 0;
                            }
                        }
                        &.indent-2 {
                            padding-left: 1.6rem;
                            @media (max-width: 600px) {
                                padding-left: 0;
                            }
                        }
                    }
                }

                figure {
                    width: 18rem;
                    @media (max-width: 1150px) {
                        width: 13.5rem;
                    }
                    @media (max-width: 600px) {
                        width: 10rem;
                        flex: 1;
                    }

                    img {
                        width: 100%;
                    }
                }

                h3 {
                    width: 20rem;
                    display: block;
                    font-size: 1.33333rem;
                    text-align: right;
                    margin-right: -2.5rem;
                    @media (max-width: 1150px) {
                        font-size: calc(1.33333rem * 0.75);
                        margin-right: -2rem;
                    }
                    @media (max-width: 600px) {
                        width: auto;
                        font-size: 0.8rem;
                        margin-right: 0;
                        flex: 1;
                    }

                    span {
                        display: block;
                        &.indent-1 {
                            padding-right: 0.8rem;
                            @media (max-width: 600px) {
                                padding-right: 0;
                            }
                        }
                        @media (max-width: 600px) {
                            display: inline;
                        }
                    }
                }
 
            }
        }
        
    }
}

.partners {
    margin: 4rem 0 1rem;
    .wrap {

        h2 {
            color: $primary-color;
            opacity: 0.5;
            text-transform: uppercase;
            font-size: 1.333rem;
            margin: 2rem 0;
            @media (max-width: 700px) {
                font-size: 1rem;
            }
        }

        .partner-logos {
            display: flex;
            align-items: center;
            flex-wrap: wrap;
            justify-content: flex-start;
            @media (max-width: 700px) {
                justify-content: center;
            }
            img {
                width: auto;
                height: 3rem;
                margin: 1rem;
                @media (max-width: 700px) {
                    height: 2rem;
                    margin: 0.5rem;
                }
            }

        }

    }
}

.certs {
    margin: 4rem 0 0;
    .wrap-narrow {
        display: flex;
        align-items: center;
        flex-wrap: wrap;
        justify-content: space-evenly;

        img {
            width: auto;
            height: 3.5rem;
            margin: 1rem;

            @media (max-width: 600px) {
                height: 2rem;
                margin: 0.5rem;
            }
        }
    }

}

.landing-link {
    margin: 4rem 0;
    .landing-link-container {
        border: $border-style;
        border-radius: $border-radius;
        padding: 2rem 2rem 0;
        text-align: center;

        img {
            width: 7rem;
        }

        h2 {
            text-transform: uppercase;
            color: $primary-color;
        }

        .btn {
            margin-bottom: -1rem;
        }
    }
}

/* INTRO */

.services-section {
    .service-subsection {
        margin-bottom: 3rem;
        .service-subsection-img img {
            width: 100%;
            border-radius: $border-radius;
        }

        .services {
            margin-top: -2.5rem;
            background: $white-color;
            display: inline-flex;
            padding: 1rem 2rem 0 0;
            border-radius: 0 $border-radius 0 0;

            h3 {
                color: $secondary-color;
                width: min-content;
            }

            img{
                width: 3.5rem;
            }
        }
    }
}

/* SERVICE */

.form-section.simple-form {
    h2 {
        font-weight: $font-normal;
        text-transform: uppercase;

        span {
            font-weight: $font-bold;
            text-decoration: underline;
        }
    }

    .form-phone {
        grid-column: 1 / 2;
    }

    .form-subject {
        grid-column: 2 / 3;
        margin: 0;
        @media (max-width: 600px) {
            grid-column: 1 / 2;
        }  
    }
}

.content-section {

    .multi-column-grid {
        display: grid;
        grid-template-columns: 2fr 1fr;
        grid-template-rows: auto 1fr;
        gap: 2rem;
        @media (max-width: 900px) {
            grid-template-columns: 1fr;
            grid-template-rows: auto;
        }
    
        .mc-grid-1 {
            grid-column: 1 / 2;
            grid-row: 1 / 3; // Span two rows
            @media (max-width: 900px) {
                grid-row: 2 / 3;
            }
            @media (max-width: 1400px) and (min-width: 981px) {
                padding-left: 2rem;
            }
        }
    
        .mc-grid-2 {
            grid-column: 2 / 3;
            grid-row: 1 / 2;
            align-self: start;
            @media (max-width: 900px) {
                grid-column: 1 / 2;
                grid-row: 1 / 2;
            }
        }
    
        .mc-grid-3 {
            grid-column: 2 / 3;
            grid-row: 2 / 3;
            align-self: start;
            @media (max-width: 900px) {
                grid-column: 1 / 2;
                grid-row: 3 / 4;
            }
        }

        h3 {
            font-size: 1rem;
            margin-top: 2rem;
        }

        img {
            height: 3rem;
            margin-right: 1rem;
        }
    
    }

    .cta-bg {
        background-color: $secondary-color;
        padding: 2rem;
        border-radius: $border-radius;

        h3 {
            color: $white-color;
            text-transform: none;
            font-size: 1.222rem;
            margin-top: 0;
        }

        a {
            color: $white-color;
            font-size: 2.5rem;
            text-decoration: none;
            font-weight: $font-bold;
            @media (max-width: 1260px) {
                font-size: 1.9rem;
            }
        }
    }

    .pdf-download {
        color: $primary-color;
        text-decoration: none;
        text-transform: uppercase;
        font-weight: $font-bold;
        display: flex;
        gap: 1rem;
        align-items: center;
        margin-top: 2rem;
        font-size: 1rem;

        &:hover {
            color: $secondary-color;
        }

        i {
            font-size: 3rem;
        }
    }
}

/* SUPPPORT */

.doc-downloads {
    margin: 4rem 0;

    .downloads-subsection {

        h3 {
            font-size: 1.333rem;
            position: relative;
            padding-left: 2.2rem;
            margin-bottom: 3rem;
            margin-top: 3rem;
            text-transform: none;

            &:before {
                content: '...';
                color: $secondary-color;
                position: absolute;
                left: -0.2rem;
                bottom: -0.3rem;
                font-size: 2.7rem;
                font-family: serif;
            }
        }

        ul {
            columns: 2 auto;
            column-gap: 3rem;
            margin-bottom: 3rem;
            @media (max-width: 700px) {
                columns: 1 auto;
            }

            li {
                margin-bottom: 0.5rem;

                a {
                    text-decoration: none;
                    color: $black-color;

                    &:after {
                        background-color: $black-color;
                    }
                }
            }
        }

    }
    
    hr {
        height: 0.4rem;
        border-radius: 0.2rem;
        background: $primary-color-light;
        border: none;
        position: relative;
        margin: 0;
        width: calc(100% - 2rem);
        overflow: visible;
    
        &:after {
            content: '...';
            color: $secondary-color;
            position: absolute;
            right: -2rem;
            bottom: -0.6rem;
            font-size: 2.7rem;
            font-family: serif;
        }
    }
}

/* PRODUCTS LIST */

.product-col-1 {
    flex-basis: 0;
    flex-grow: 2;
    @media (max-width: 1400px) and (min-width: 981px) {
        padding-left: 2rem;
    }
}

.product-col-2 {
    flex-basis: 0;
    flex-grow: 5;
}

.product-list-intro {
    margin-top: 3rem;
    .wrap {
        display: flex;
        gap: 2rem;
        align-items: center;
        @media (max-width: 800px) {
            flex-direction: column;
        }

        .product-col-1 {
            display: flex;
            align-items: center;
            justify-content: space-around;
            @media (max-width: 800px) {
                margin-top: 2rem;
            }

            h1 {
                width: min-content;
                @media (max-width: 800px) {
                    font-size: 1.5rem;
                }
            }
        }

    }
}

.product-list-section {
    margin: 4rem 0;
    .wrap {
        display: flex;
        gap: 2rem;
        @media (max-width: 700px) {
            flex-direction: column;
        }
    }

    &.product-list-section-reverse {
        .wrap {
            @media (max-width: 700px) {
                flex-direction: column-reverse;
            }
        }
    }

    .category-filters {
        position: sticky;
        top: 5rem;
    }

    .searchbox {
        margin-bottom: 2rem;
    }

    h4 {
        font-size: 1.333rem;
        text-transform: uppercase;
        color: $primary-color;
        margin: 2rem 0;
    }

    .product-categories {
        padding-left: 0;

        li {
            margin-bottom: 0.6rem;
            &:before {
                display: none;
            }

            ul {
                margin-top: 0.6rem;
                position: relative;

                &:before {
                    content: '';
                    display: block;
                    height: 100%;
                    width: 10px;
                    left: 0;
                    border-radius: 1rem;
                    background: $primary-color;
                    position: absolute;
                }

                &.sub-categories {
                    height: 0;
                    overflow: hidden;
                    margin-top: 0;
                }
            }

            a {
                text-decoration: none;
                color: $black-color;

                &:after {
                    background: $black-color;
                }
            }

            &.active-list-item {
                & > a {
                    font-weight: $font-bold;
                    color: $secondary-color;
                }

                .sub-categories {
                    height: auto;
                    margin-top: 0.6rem;
                    overflow: visible;
                }
            }
        }
    }

    .checkbox-list {
        padding-left: 0;
        li {
            margin-bottom: 0.6rem;
            display: flex;
            &:before {
                display: none;
            }

            label {
                padding-left: 1rem;
            }
        }
    }

    .product-card-list {
        display: grid;
        width: 100%;
        max-width: 100%;
        grid-template-columns: repeat(2, minmax(0, 1fr));
        gap: 4rem 2rem;
        @media (max-width: 1050px) {
            grid-template-columns: repeat(1, minmax(0, 1fr));
        }

        .termek {
            border: $border-style;
            border-radius: $border-radius;
            position: relative;

            .featured-img {
                width: 100%;
                border-radius: $border-radius $border-radius 0 0;
            }

            .product-card-content {
                display: flex;
                flex-direction: column;
                margin: 1rem 2rem 0;

                h2 {
                    font-size: 1.7777rem;
                    margin-bottom: 1rem;
                }

                .btn {
                    margin: 1rem auto -1.5rem;
                }
            }
        }
    }
}

.atex-img {
    position: absolute;
    top: 1rem;
    right: 1rem;
    height: 4rem;
}

.termek {

    h1 {
        margin-top: 0;
        margin-bottom: 3rem;
    }

    h5 {
        font-size: 1.2rem;
        font-weight: $font-bold;
        color: $primary-color;
        margin-bottom: 1rem;
    }

    .product-head {
        display: flex;
        gap: 2rem;
        @media (max-width: 1050px) {
            flex-direction: column;
        }

        .product-card-img {
            position: relative;
            flex-basis: 0;
            flex-grow: 1;
            display: flex; /* Ha a tartalom középre helyezése kell */
            flex-direction: column;

            .featured-img {
                max-width: 100%;
                height: auto;
                display: block;
                border: $border-style;
                border-radius: $border-radius;
            }
            .product-card-gallery {
                display: flex;
                gap: 1rem;
                padding: 1rem 0;
                img {
                    width: auto;
                    height: 5rem;
                    border: $border-style;
                    border-radius: $border-radius;
                    cursor: pointer;

                    &:hover {
                        border-color: $secondary-color;
                        border-width: 3px;
                    }

                    &.current-gallery-item {
                        border-color: $primary-color;
                        border-width: 3px;
                    }
                }
            }
        }

        .product-card-content {
            flex-basis: 0;
            flex-grow: 1;
            padding-bottom: 2rem;

            .btn {
                margin-top: 1rem;
            }
        }

    }

    .product-downloads {
        background-color: $primary-color-light;
        border-radius: $border-radius;
        padding: 2rem;
        margin-top: 2rem;


        h3 {
            margin: 0;
            font-size: 1.333rem;
            display: flex;
            align-items: center;
            gap: 0.5rem;

            .fa-file-download {
                font-size: 2.5rem;
                margin-right: 1rem;
            }
            .accordion-arrow {
                font-size: 2rem;
                margin-left: auto;
                transition: 1s all;
            }

            &:hover {
                color: $secondary-color;
                cursor: pointer;
            }

            &.expanded-h3 {
                
                .accordion-arrow {
                    transform: rotate(180deg);
                    transition: 1s all;
                }
            }
        }

        .product-downloads-content {
            max-height: 0px;
            overflow: hidden;
            transition: max-height 1s ease;

            &.dl-expanded {
                max-height: 1000px;
                overflow: auto;
                transition: max-height 1s ease;
            }
        }

        h4 {
            text-transform: none;
            font-size: 1.111rem;
            margin-bottom: 0;
        }

        ul li {
            &::before {
                display: none;
            }
            a {
                text-decoration: none;
                color: $black-color;

                &::after {
                    background: $black-color;
                }
            }
        }
    }

}

.related-products {
    margin-top: 2rem;
    h3 {
        margin-top: 0;
        margin-bottom: 2rem;
        font-size: 1.333rem;
        display: flex;
        align-items: center;

        &:after {
            left: 4rem;
            bottom: -1.2rem;
        }

        i {
            font-size: 2.5rem;
            margin-right: 1rem;
        }
    }

    .related-cards {
        display: grid;
        width: 100%;
        max-width: 100%;
        grid-template-columns: repeat(3, minmax(0, 1fr));
        gap: 1rem;
        @media (max-width: 1200px) {
            grid-template-columns: repeat(2, minmax(0, 1fr));
        }
        @media (max-width: 1050px) {
            grid-template-columns: repeat(1, minmax(0, 1fr));
        }

        .related-card {
            border: $border-style;
            border-radius: $border-radius;

            a {
                text-decoration: none;

                img {
                    width: 100%;
                    border-radius: $border-radius $border-radius 0 0;
                }

                h4 {
                    padding: 1rem;
                    margin: 0;
                }

                &:hover {
                    h4 {
                        color: $secondary-color;
                    }
                }
            }
        }
    }

}

/* CONTACT */

.contact-infos {
    .contact-info-container {
        display: flex;
        gap: 2rem;
        justify-content: space-between;
        flex-wrap: wrap;

        div {
            flex-basis: 0;
            flex-grow: 1;
        }

        h3 {
            margin-bottom: 2rem;
        }
    }
}

.maps {
    color: $white-color;
    margin: 2rem 0;
    .wrap-wide {
        background: $primary-color;
        padding-top: 2rem;
        padding-bottom: 2rem;
        position: relative;
    }
    .wrap {
        margin-right: auto;
        margin-left: 0;
    }

    .maps-container {
        display: flex;
        gap: 2rem;
        @media (max-width: 800px) {
            flex-direction: column;
        }

        p {
            margin-top: 0;
        }

        iframe {
            width: 100%;        
        }

        .map-1, .map-2 {
            flex-grow: 1;
            flex-basis: 0;
        }
    }
}

.team {
    margin: 4rem 0;

    h3 {
        text-align: center;
        font-size: 2rem;
    }

    .team-members {
        display: flex;
        justify-content: center;
        flex-wrap: wrap;
        gap: 4rem;
        margin-top: 2rem;
        padding: 3rem 0;

        .team-member {
            text-align: center;

            .team-member-name {
                font-size: 1.333rem;
                text-transform: uppercase;
                font-weight: $font-bold;
                color: $primary-color;
                margin-bottom: 0;
                display: block;
            }

            .team-member-position {
                font-style: italic;
                display: block;
                position: relative;
                margin-bottom: 2rem;

                &:after {
                    content: '';
                    width: 10px;
                    height: 4px;
                    border-radius: 2px;
                    background-color: $secondary-color;
                    display: block;
                    position: absolute;
                    bottom: -1rem;
                    left: 50%;
                    margin-left: -5px;
                }
            }

            .team-member-phone {
                display: block;
                margin-top: 0.5rem;
            }

            p {
                margin: 0;
            }
        }
    }
}

/* APPLICATIONS */

.app-sections {

    .app-section {
        scroll-margin-top: 60px;

        .wrap-wide {
            position: relative;
            margin-top: 2rem;
            margin-bottom: 4rem;

            .wrap {
                display: flex;
                max-width: calc($wrap-width - 2 * $wrap-padding);
                margin-right: 0;
                gap: 2rem;
                @media (max-width: 980px) {
                    flex-direction: column;
                }

                .app-img {
                    flex-basis: 0;
                    flex-grow: 1;
                    position: relative;
                }

                figure {
                    position: absolute;
                    top: 0;
                    right: 0;
                    width: 50vw;
                    max-width: calc($wrap-width / 2 + (1500px - $wrap-width) + $wrap-padding / 2);
                    height: 100%;
                    @media (max-width: 980px) {
                        position: relative;
                        width: calc(100% + $wrap-padding);
                        max-width: initial;
                        height: auto;
                        left: -2rem;
                    }

                    img {
                        width: 100%;
                        height: 100%;
                        object-fit: cover;
                        border-radius: 0 $border-radius $border-radius 0;
                        @media (min-width: 1700px) {
                            border-radius: $border-radius;
                        }
                    }
                }

                .app-text {
                    flex-basis: 0;
                    flex-grow: 1;

                    h3 {
                        font-size: 2rem;
                        text-transform: uppercase;                        
                        margin-top: 0;
                    }
                }
            }

            &.wrap-reverse {
                .wrap {
                    flex-direction: row-reverse;
                    margin-right: auto;
                    margin-left: 0;
                    @media (max-width: 980px) {
                        flex-direction: column;
                    }

                    figure {
                        left: 0;
                        @media (max-width: 980px) {
                            position: relative;
                            width: calc(100% + $wrap-padding);
                            height: auto;
                        }
    

                        img {
                            border-radius: $border-radius 0 0 $border-radius;
                            @media (min-width: 1700px) {
                                border-radius: $border-radius;
                            }
                        }
                    }
                }
            }

        }

    }

}

.form-section {
    .wrap-wide {
        background: url('../img/form-bg.webp') no-repeat $primary-color;
        background-size: cover;
        background-position: right;
        padding: 2rem 0;

        .wrap-narrow {
            position: relative;
        }
    }

    h2 {
        color: $white-color;
        margin-top: 0;
    }

    .form-ma-logo {
        height: 3rem;
        position: absolute;
        right: $wrap-padding;
        bottom: 0
    }

    form {
        display: grid;
        grid-template-columns: 1fr 1fr;
        gap: 1rem;
        @media (max-width: 600px) {
            grid-template-columns: 1fr;
            gap: 1rem 0;
        }

        p {
            margin: 0;
            padding: 0;
        }

        label {
            position: absolute;
            width: 1px;
            height: 1px;
            padding: 0;
            margin: -1px;
            overflow: hidden;
            clip: rect(0, 0, 0, 0);
            border: 0;
        }

        input {
            width: 100%;
            max-width: 100%;
            font-size: 1rem;
            padding: 0.8888rem;
            border-radius: $border-radius;
            border: none;
        }

        input[type="submit"] {
            width: auto;
            max-width: auto;
            padding: 1.11111rem 1.66667rem;
        }

        select {
            width: 100%;
            font-size: 1rem;
            padding: 0.8888rem;
            border-radius: $border-radius;
            border: none;
        }

        textarea {
            width: 100%;
            font-size: 1rem;
            padding: 0.8888rem;
            border-radius: $border-radius;
            height: 100%;
        }

        .form-name {
            grid-column: 1 / 2;
        }

        .form-email {
            grid-column: 2 / 3;
            @media (max-width: 600px) {
                grid-column: 1 / 2;
            }   
        }

        .form-target {
            grid-column: 1 / 2;
        }

        .form-phone {
            grid-column: 2 / 3;
            @media (max-width: 600px) {
                grid-column: 1 / 2;
            } 
        }

        .form-subject {
            grid-column: 1 / 3;
            margin: 1rem 0;
        }

        .form-message {
            grid-column: 1 / 3;
        }

    }
}

footer {
