}

figure {
  margin: 0;
}

fieldset {
  border: 1px solid #c0c0c0;
  margin: 0 2px;
  padding: 0.35em 0.625em 0.75em;
}

legend {
  border: 0;
  padding: 0;
  white-space: normal;
}

button,
input,
select,
textarea {
  font-family: inherit;
  font-size: 100%;
  margin: 0;
  vertical-align: baseline;
}

button, input {
  line-height: normal;
}

button,
select {
  text-transform: none;
}

button,
html input[type=button],
input[type=reset],
input[type=submit] {
  -webkit-appearance: button;
  cursor: pointer;
}

button[disabled],
input[disabled] {
  cursor: default;
}

button::-moz-focus-inner, input::-moz-focus-inner {
  border: 0;
  padding: 0;
}

textarea {
  overflow: auto;
  vertical-align: top;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

input[type=search] {
  -webkit-appearance: textfield;
  -moz-box-sizing: content-box;
  -webkit-box-sizing: content-box;
  box-sizing: content-box;
}
input[type=search]::-webkit-search-cancel-button, input[type=search]::-webkit-search-decoration {
  -webkit-appearance: none;
}
input[type=checkbox], input[type=radio] {
  box-sizing: border-box;
  padding: 0;
}

* {
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
  scroll-padding-top: 5rem;
}

body {
  font-family: "Rubik", sans-serif;
  font-size: 18px;
  overflow-x: hidden;
  padding-top: 5rem;
}
body.admin-bar .site-header {
  top: 32px; /* Moves the header down */
}

.screen-reader-text {
  position: absolute;
  left: -9999px;
  top: 0;
}

.fancy-anchor a, p a, .contact-list a, a.fancy {
  display: inline-block;
}
.fancy-anchor a:after, p a:after, .contact-list a:after, a.fancy:after {
  content: "";
  width: 0;
  height: 2px;
  display: block;
  background: #FFFFFF;
  border-radius: 2px;
  position: relative;
  top: 2px;
  transition: 1s all ease;
}
.fancy-anchor a:hover:after, p a:hover:after, .contact-list a:hover:after, a.fancy:hover:after {
  width: 100%;
  transition: 1s all ease;
}

p, .contact-list {
  line-height: 1.4;
}
p a, .contact-list a {
  color: #212E5B;
  text-decoration: none;
  font-weight: 700;
}
p a:after, .contact-list a:after {
  background: #212E5B;
  top: 0px;
}

h1, h2, h3 {
  color: #212E5B;
  text-transform: uppercase;
}

h2 {
  font-size: 2rem;
}

dl.contact-list dt {
  font-weight: 700;
  margin-bottom: 0.25rem;
}
dl.contact-list dd {
  margin-bottom: 0.5rem;
  margin-left: 0;
  font-weight: 300;
}

.ul-dots {
  position: relative;
}
.ul-dots:after {
  content: "...";
  color: #C41D48;
  position: absolute;
  left: 0;
  right: 0;
  bottom: -1.5rem;
  font-size: 2.7rem;
  font-family: serif;
}

.fixed-offer-btn {
  transform: translateY(-50%);
  writing-mode: vertical-rl;
  text-transform: uppercase;
  position: fixed;
  left: -1.2rem;
  top: 50%;
  background: #C41D48;
  color: #FFFFFF;
  padding: 1rem 1rem 1rem 2rem;
  z-index: 9999;
  border-radius: 0 0.28rem 0.28rem 0;
  text-decoration: none;
  font-weight: 700;
  transition: all 0.5s ease;
}
@media (max-width: 980px) {
  .fixed-offer-btn {
    right: -1.2rem;
    left: auto;
    top: auto;
    bottom: 10%;
    transform: rotate(180deg);
  }
}
.fixed-offer-btn:hover {
  left: -0.7rem;
  transition: all 0.5s ease;
}
@media (max-width: 980px) {
  .fixed-offer-btn:hover {
    left: auto;
    right: -0.7rem;
  }
}

.nav-dots {
  position: absolute;
  bottom: -1rem;
  right: 2rem;
  background-color: #FFFFFF;
  border-radius: 0.28rem 0.28rem 0 0;
  z-index: 9999;
  display: flex;
  gap: 1rem;
  padding: 0.5rem 0.5rem 0 1rem;
  align-items: center;
}
.nav-dots button {
  box-shadow: none;
}
.nav-dots .my-nav {
  display: flex;
  gap: 0.5rem;
}
.nav-dots .my-nav button {
  padding: 0.5rem 1rem;
}
.nav-dots .my-dots {
  display: flex;
  gap: 0.5rem;
}
.nav-dots .my-dots button {
  padding: 0;
  width: 10px;
  height: 10px;
  border-radius: 5px;
}
.nav-dots .my-dots button.active {
  background: #C41D48;
}

.searchbox {
  position: relative;
}
.searchbox i {
  position: absolute;
  top: 0.75rem;
  left: 0.6rem;
  color: #FFFFFF;
  font-size: 2rem;
}
.searchbox .search-field {
  background-color: #212E5B;
  width: 100%;
  padding: 1rem 1rem 1rem 3.5rem;
  border: none;
  border-radius: 0.28rem;
