}

main button, .btn, input[type=submit] {
  background: #212E5B;
  color: #FFFFFF;
  border: none;
  padding: 1.11111rem 1.66667rem;
  border-radius: 0.28rem;
  font-size: 1rem;
  text-transform: lowercase;
  font-weight: 700;
  display: inline-block;
  cursor: pointer;
  box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.25);
  transition: all 0.5s ease;
  width: auto;
  justify-self: start;
  text-decoration: none;
}
main button:hover, .btn:hover, input[type=submit]:hover {
  background: #C41D48;
}
main button.red, .btn.red, input[type=submit].red {
  background: #C41D48;
}
main button.red:hover, .btn.red:hover, input[type=submit].red:hover {
  background: #212E5B;
}

ul {
  padding-left: 1.5rem;
  line-height: 1.4;
}
ul li {
  list-style: none;
  position: relative;
}
ul li:before {
  content: "";
  width: 10px;
  height: 4px;
  border-radius: 2px;
  background-color: #C41D48;
  display: block;
  position: absolute;
  top: 0.65rem;
  left: -1.5rem;
}

.wrap {
  max-width: 1350px;
  margin: 0 auto;
  width: 100%;
  padding: 0 2rem;
}

.wrap-narrow {
  max-width: 900px;
  margin: 0 auto;
  width: 100%;
  padding: 0 2rem;
}

.wrap-wide {
  position: relative;
  width: calc(100% - 2rem);
  border-radius: 0 0.28rem 0.28rem 0;
}
@media (min-width: 1350px) {
  .wrap-wide {
    width: calc(100% - (100% - 1350px) / 2 - 2rem);
  }
}
@media (min-width: 1700px) {
  .wrap-wide {
    width: 1500px;
    left: calc((100% - 1350px) / 2 - (150px + 2rem));
    border-radius: 0.28rem;
  }
}
@media (min-width: 1700px) {
  .wrap-wide .full-img {
    border-radius: 0.28rem;
  }
}
.wrap-wide.wrap-reverse {
  left: 2rem;
  border-radius: 0.28rem 0 0 0.28rem;
}
@media (min-width: 1350px) {
  .wrap-wide.wrap-reverse {
    left: calc((100% - 1350px) / 2 + 2rem);
  }
}
@media (min-width: 1700px) {
  .wrap-wide.wrap-reverse {
    border-radius: 0.28rem;
  }
}
.wrap-wide .wrap {
  max-width: calc(1350px - 2rem);
}
.wrap-wide .wrap.wrap-absolute {
  position: absolute;
  top: 0;
  right: 0;
}
.wrap-wide .wrap.with-narrow {
  max-width: calc(1350px - 4rem);
  padding: 0;
  margin: 0 0 0 auto;
}
@media (max-width: 1350px) and (min-width: calc(1350px - 2rem)) {
  .wrap-wide .wrap.with-narrow {
    margin-left: calc(2rem - 0.5 * (1350px - 100vw));
  }
}
@media (max-width: calc(1350px - 2rem)) and (min-width: calc(900px + 2rem)) {
  .wrap-wide .wrap.with-narrow {
    margin-left: 1rem;
  }
}
@media (max-width: calc(900px + 2rem)) and (min-width: 900px) {
  .wrap-wide .wrap.with-narrow {
    margin-left: calc(1rem - 0.5 * (900px + 2rem - 100vw));
  }
}
@media (max-width: 900px) {
  .wrap-wide .wrap.with-narrow {
    margin-left: 0;
  }
}
.wrap-wide .wrap-narrow {
  width: 100%;
}

